  display: block;
  padding-top: 2px;
}
.m-tabbar .m-tabbar-item .m-tabbar-item-icon img {
  width: 28px;
  height: 28px;
}
.m-tabbar .m-tabbar-item .m-tabbar-item-text {
  display: block;
  font-size: 10px;
  color: #949494;
}
.m-tabbar .m-tabbar-item.is-active .m-tabbar-item-text {
  color: #42bd56;
}

